.share-link {
    margin-right: 1em;
    &:last-child {
        margin-right: 0;
    }
}

.footer__social {
    border: solid 1px #dbdbdb;
    color: #dbdbdb;
    display: inline-block;
    margin-top: 1em;
    padding: .75em;
    .like-us {
        color: #dbdbdb;
        display: inline-block;
        margin-right: 1em;
        &:last-child {
            margin-right: 0;
        }
    }
    @media screen and (min-width: 736px) {
        margin-top: 0;
    }
}

.follow__social__copy {
    display: inline-block;
    line-height: 2em;
    margin-right: 1em;
    vertical-align: text-bottom;
}
