.box {
    border: solid 1px #dbdbdb;
    margin-bottom: 2em;
    padding: 1.5em;

    > :last-child {
        margin-bottom: 0;

        > :last-child {
            margin-bottom: 0;

            > :last-child {
                margin-bottom: 0;
            }
        }
    }

    &.alt {
        border: 0;
        border-radius: 0;
        padding: 0;
    }

    &.person {
        border: solid 1px #8dcca9;
        padding: 3em 1.5em;

        h3 {
            margin: 0;
        }

        .image {
            margin-bottom: 1em;

            img {
                max-width: 100%;
            }
        }
    }
}

.box-sm {
    padding: 1em;
}
